require('../assets/scss/iconfont/iconfont.css');
    var $ = require('webpack-zepto');

    module.exports = {
        data: function () {
            var self = this;
            return {
                topic: {
                    tab:'share',
                    title:'',
                    content:'',
                    accesstoken:localStorage.token
                },
                err:'',
                authorTxt:'<br/><br/><a class="from" href="https://github.com/shinygang/Vue-cnodejs">I‘m webapp-cnodejs-vue</a>',
                alert: {
                    txt: '',
                    show: false,
                    hideFn:function(){
                        var timer;
                        clearTimeout(timer);
                        timer = setTimeout(function () {
                            self.alert.show = false;
                        }, 1000);
                    }
                }
            }
        },
        methods: {
            addTopic:function(){
                var self = this
                    , title = $.trim(self.topic.title)
                    , contents = $.trim(self.topic.content);
                if(!title || title.length < 10){
                    self.err = 'title';
                    return false;
                }
                if(!contents){
                    self.err = 'content';
                    return false;
                }
                self.topic.content = self.topic.content+self.authorTxt;
                $.ajax({
                    type:'POST',
                    url:'https://cnodejs.org/api/v1/topics',
                    data: self.topic,
                    dataType: 'json',
                    success:function(res){
                        if(res.success){
                            self.$route.router.go({name:'home'});
                        }
                    },
                    error:function(res){
                        var error = JSON.parse(res.responseText);
                        self.alert.txt = error.error_msg;
                        self.alert.show = true;
                        self.alert.hideFn();
                        return false;
                    }
                });
            }
        },
        components:{
            "nvHead":require('../components/header.vue'),
            "nvAlert":require('../components/nvAlert.vue')
        }
    }


