require('../assets/scss/CV.scss');
    var $ = require('webpack-zepto');

    module.exports = {
        data: function(){
            return {
                showMenu: false,
                scroll:true,
                topics:[],
                searchKey:{
                	page:1,
                	limit:20,
                	tab:'all',
                	mdrender:true
                }
            }
        },
        route:{
        	data:function(transition){
                var _self = this;

                //_self.searchKey.page = 1;
                _self.searchKey.limit = 20;
        		if(transition.to.query.tab){
                    _self.searchKey.tab = transition.to.query.tab;
                }

                _self.showMenu = false;

                //页面初次加载获取的数据
                _self.getTopics();

                //滚动加载
                $(window).on('scroll', function() {
                    _self.getScrollData();
                });
               
        	},
            deactivate:function(transition){
                $(window).off('scroll');
                transition.next();
            }
        },
        methods:{
            getTopics:function(){
                var _self = this
                    , params = $.param(_self.searchKey);
                $.get('https://cnodejs.org/api/v1/topics?'+params,function(d){
                    _self.scroll = true;
                    if(d && d.data){
                        _self.topics = d.data;
                        // if(_self.searchKey.page == 0){
                        //     _self.topics = d.data;
                        // }
                        // else{
                        //     _self.topics = _self.topics.concat(d.data);
                        // }
                    }
                })
            },
            //滚动加载数据
            getScrollData:function(){
                var _self = this;
                if(_self.scroll){
                    var totalheight = parseFloat($(window).height()) + parseFloat($(window).scrollTop());
                    if ($(document).height() <= totalheight + 100) {
                        _self.scroll = false;
                        _self.searchKey.limit += 20;
                        _self.getTopics();
                    }
                }
            }
        },
        components:{
            "nvHead":require('../components/header.vue')
        }
    }


